.box-total {
    height: 70vh;
    margin-bottom: 50vh;
}

.box-small-img {
    display: "flex";
    flex-direction: column;
}

.box-small-img img {
    border: 1px solid #f7f7f8;
    margin: 5px;
    width: 150px;
    height: 150px;
    cursor: pointer;
}

.box-small-img img:hover {
    border: 1px solid black;
}

.box-main-img img {
    /* border: 1px solid black; */
    width: 600px;
    height: 600px;
    margin-top: 5px;
}

.box-content {
    border: none;
    margin-top: 5px;
}

.title-product {
    font-size: 30px;
    font-weight: bold;
    font-family: 'Times New Roman', Times, serif;
}

.box-input-value {
    display: flex;
    margin-bottom: 10px;
}

.btnSub,
.btnAdd {
    /* padding: 0 10px; */
    border: 1px solid #F5F5F5;
    background-color: #F5F5F5;
    width: 30%;
    font-size: 2rem;
    font-weight: bold;
}

.box-instock {
    margin-left: 30px;
    margin-top: 28px;
    width: 30%;
    font-size: 1rem;
    font-weight: bold;
}

.box-button {
    display: flex;
}

.txtInput {
    width: 80px;
    text-align: center;
    border: 1px solid #F5F5F5;
    /* padding: 0 10px; */
    width: 30% !important;
    font-size: 2rem;
    font-weight: bold;
}

.box-alert {
    margin-left: 20px;
    padding: 1px 10px;
    height: 40px;
}
.css-dcrfqy-MuiPaper-root-MuiAlert-root{
    /* font-size: 0.4rem !important; */
    width: 80%;
}
.css-1erij54{
    font-size: 0.4rem !important;
}
.css-mhc70k-MuiGrid-root {
    margin-top: 30px !important;
}
.css-isbt42{
    margin-top: 30px !important;
}
.product-breadcrumb {
    background: #f5f5f5;
    height: 40px;
    padding-left: 75px;
}

.product-text-breadcrumb {
    list-style: none;
    padding: 0.75rem 1rem;
}
.product-best-mobile {
    font-size: 2.5rem;
    font-weight: 500;
    padding: 5% 0;
    text-align: center;
  }

@media only screen and (max-width: 768px) {
    .box-main-img img {
        /* border: 1px solid black; */
        /* width: 600px; */
        height: 30vh;
        margin-top: 5px;
    }

    .box-total {
        height: 32vh;
        margin-bottom: 20vh;
    }

    .btnSub,
    .btnAdd,
    .txtInput {
        height: 10%!important;
        width: 20%;
    }

    .box-instock {
        margin: 0;
        font-size: 0.75rem;
        font-weight: bold;
        margin-left: 10px;
        margin-top: 20px;
    }
    .css-dcrfqy-MuiPaper-root-MuiAlert-root{
        font-size: 0 !important;
        width: 40%;
    }
}

@media only screen and (max-width: 576px) {
    .box-total {
        height: 30vh;
        margin-bottom: 20vh;
    }

    .box-main-img img {
        height: 20vh;
        margin-top: 5px;
    }

    .box-small-img img {
        width: 50px;
        height: 50px;
    }

    .title-product {
        font-size: 12px;
    }

    .btnSub,
    .btnAdd {
        height: 3vh !important;
        width: 10% !important;
        font-size: 12px !important;
    }

    .txtInput {
        height: 3vh !important;
        width: 20% !important;
        font-size: 16px !important;
        padding:0!important;
        margin:0!important;
    }
    .box-instock {
        font-size: 0.5rem;
        margin-left: 5px;
        margin-top: 10px;
    }
    .product-best-mobile {
        font-size: 1.5rem;
        padding-top: 30px;
      }
    /* .css-dcrfqy-MuiPaper-root-MuiAlert-root{
        width: 35%;
        padding-left: -35%;
    } */
    .css-dcrfqy-MuiPaper-root-MuiAlert-root{
        width: 33%;
        height: 10vh;
        display: block!important;
    }
    .css-ki1hdl-MuiAlert-action {
    padding: 0!important;
    }
    .css-sghohy-MuiButtonBase-root-MuiButton-root{
        width: 92%;
        height: 3vh!important;
        font-size: 0.5rem!important;
    }

}
@media only screen and (max-width: 1023.5px){
    img {
        display: block!important;
    }
}
